"use client";

import React, { createContext, useContext, useEffect, useState } from "react";

type Theme = "light" | "dark" | "system";

interface ThemeContextType {
  theme: Theme;
  setTheme: (t: Theme) => void;
}

const ThemeContext = createContext<ThemeContextType>({
  theme: "system",
  setTheme: () => {},
});

export function ThemeProvider({ children }: { children: React.ReactNode }) {
  const [theme, setThemeState] = useState<Theme>("system");

  // apply or remove the "dark" class based on effective theme
  const applyEffectiveTheme = (t: Theme) => {
    const root = window.document.documentElement;
    if (t === "system") {
      const isSystemDark = window.matchMedia(
        "(prefers-color-scheme: dark)"
      ).matches;
      root.classList.toggle("dark", isSystemDark);
    } else {
      root.classList.toggle("dark", t === "dark");
    }
  };

  useEffect(() => {
    // on mount: read stored theme or default to system
    const stored = localStorage.getItem("theme") as Theme | null;
    const initial: Theme = stored ?? "system";
    setThemeState(initial);
    applyEffectiveTheme(initial);

    // if using system, listen for changes
    const mql = window.matchMedia("(prefers-color-scheme: dark)");
    const onChange = () => {
      // only react when current mode is 'system'
      if (localStorage.getItem("theme") === "system" || initial === "system") {
        applyEffectiveTheme("system");
      }
    };
    mql.addEventListener?.("change", onChange);
    return () => mql.removeEventListener?.("change", onChange);
  }, []);

  // setter that persists and applies theme
  const setTheme = (t: Theme) => {
    localStorage.setItem("theme", t);
    setThemeState(t);
    applyEffectiveTheme(t);
  };

  return (
    <ThemeContext.Provider value={{ theme, setTheme }}>
      {children}
    </ThemeContext.Provider>
  );
}

export const useTheme = () => useContext(ThemeContext);
